import React, { Fragment } from 'react';
import JouleIcon from '../../assets/green-energy.png'
import mindsetPill from '../../assets/mindsetPillContainer.jpg'
import { menuItems } from '../../utils/constants/routes'
import { Menu, JouleContainer } from './Styled';
import { Link } from 'react-router-dom'

const SideContent = (props) => {
    return (
        <Fragment>
            <div style={{position: 'relative', display: 'flex', justifyContent: 'center', alignItems: 'center'}} >
                <img style={{width: '100%'}} alt='' src={mindsetPill} />
                <span style={{position: 'absolute', width: '50%', fontSize: '8px', transform: 'rotate(-5deg)'}} >Life is beautiful not because of the things we see or do. Life is beautiful because of the people we meet.</span>
            </div>
            <Menu>
                {menuItems.map(item => 
                    <Link to={ item.path }>{item.name}</Link> 
                )}
            </Menu>
            <JouleContainer>
                <img src={ JouleIcon } alt=''/>
                <span>
                    { props.user.wallet } JOULES 
                </span>
            </JouleContainer>
        </Fragment>
    )
}

export default SideContent